.App {
  text-align: center;
}
body {
  background-color: #ea5f46;
}
.frame {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  margin: 5em 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 220px;
}
.card {
  border-radius: 2em;
  padding: 0 1em;
  background-color: #818181;
}
.Controller {
  background: #555;
  margin: 0 1em;
  border: 1px solid;
  height: 3em;
  width: 3em;
  border-radius: 100%;
  border-color: #fff;
  background: transparent;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Control-buttons {
  height: 4em;
  width: 15em;
  background-color: #3b8edb;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Control-panel {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Panel {
  border-radius: 16px;
}
.Panel ul {
  display: table;
  margin: 0 auto;
  padding: 20px;
  list-style-type: none;
}
.Panel li a {
  display: block;
  color: #fff;
  text-align: center;
  padding: 16px;
  text-decoration: none;
  border: 0.25px solid #fff;
}
.Panel li {
  float: left;
  background-color: #3b8edb;
}
.selected {
  background-color: #04b822;
}
